import Component from '../../core/Component';
import { registerUser } from '../../auth/register';
import { setData } from '../../utils/storageUtil';
import { isPasswordDifferent } from '../../auth/validate';
import { showSnackBar } from '../../utils/domUtil';
import { ERROR_MESSAGE, NAME, PASSWORD } from '../../constant/constant';
import { globalStore } from '../../domains/GlobalStore';

class UserRegisterPage extends Component {
  template() {
    const { isloggedin } = this.props;
    const login = JSON.parse(isloggedin);

    if (login) {
      return `<p>  </p>`;
    }

    return `
      <header>
        <h1 class="title"></h1>
      </header>
      <div>
        <form id="register-form" class="register-form">
          <label for="email"></label>
          <input type="email" class="register-input styled-input" placeholder="  " name="email" required>
          <label for="user-name"></label>
          <input type="text" class="register-input styled-input" placeholder=" " name="userName" minlength=${NAME.MIN_LENGTH} maxlength=${NAME.MAX_LENGTH} required>
          <label for="password"></label>
          <input type="password" class="register-input styled-input" placeholder=" " name="password" pattern=${PASSWORD.PATTERN} required>
          <label for="password-check"> </label>
          <input type="password" class="register-input styled-input" placeholder=" " name="passwordCheck" pattern=${PASSWORD.PATTERN} required>
          <button type="submit" class="register-button styled-button emphasized"></button>
        </form>
      </div>
      <div class="snackbar"></div>
    `;
  }

  setEvent() {
    this.addEvent('submit', '#register-form', async (event) => {
      event.preventDefault();

      const { email, userName, password, passwordCheck } =
        event.target.elements;

      if (isPasswordDifferent(password.value, passwordCheck.value)) {
        showSnackBar(ERROR_MESSAGE.CHECK_PASSWORD);

        return;
      }

      const response = await registerUser({
        email: email.value,
        name: userName.value,
        password: password.value,
      });

      if (!response.accessToken) {
        showSnackBar(response);

        return;
      }

      setData('user', response);
      const to = '/';
      const state = { path: to };

      window.history.pushState(state, '', to);
      globalStore.login(response);
    });
  }
}

customElements.define('user-register', UserRegisterPage);
