import Component from '../../core/Component';
import { vendingMachine } from '../../domains/VendingMachine';
import { AMOUNT, MONEY_UNIT } from '../../constant/constant';
import { sortCoins } from '../../utils/coinUtil';
import { showSnackBar } from '../../utils/domUtil';

class ChangeChargePage extends Component {
  template() {
    const coins = vendingMachine.useStore((state) => state.coins);
    const coinArray = sortCoins(coins);
    const totalMoney = vendingMachine.getTotalMoney();

    return `
      <section>
        <h2 hidden> </h2>
        <form id="change-charge-form" class="change-charge-form">
          <div>
            <label for="amount" class="description">   .</label>
            <input
              id="charge-amount"
              class="charge-amount-input styled-input"
              name="amount"
              placeholder=""
              type="number"
              min="${AMOUNT.MIN}"
              max="${AMOUNT.MAX}"
              step="${MONEY_UNIT}"
              required
              autofocus
            >
          </div>
          <button class="add-charge-button styled-button emphasized"></button>
        </form>
        <p class="current-money-indicator">  : <span>${totalMoney}</span></p>
      </section>
      <section>
        <h2 class="table-title">  </h2>
        <table class="styled-table">
          <thead>
            <tr class="styled-tr">
              <th class="styled-th"></th>
              <th class="styled-th"></th>
            </tr>
          </thead>
          <tbody>
            ${coinArray
              .map(
                ([key, value]) => `
                  <tr class="styled-tr">
                    <td class="styled-td">${key}</td>
                    <td class="styled-td">${value}</td>
                  </tr>
                `
              )
              .join('')}
          </tbody>
        </table>
      </section>
      <div class="snackbar"></div>
    `;
  }

  setEvent() {
    this.addEvent('submit', '#change-charge-form', (event) => {
      event.preventDefault();

      const chargeAmount = this.querySelector('#charge-amount').valueAsNumber;

      try {
        vendingMachine.addCoin(chargeAmount);
      } catch (err) {
        showSnackBar(err);
      }
    });
  }
}

customElements.define('change-charge', ChangeChargePage);
