import Component from '../../core/Component';
import { vendingMachine } from '../../domains/VendingMachine';
import '../components/ItemRow';
import { ITEM, MONEY_UNIT } from '../../constant/constant';
import { showSnackBar } from '../../utils/domUtil';

class ItemManagementPage extends Component {
  template() {
    const items = vendingMachine.useStore((state) => state.items);

    return `
      <section>
        <h2 hidden>  </h2>
        <form id="item-add-form" class="item-add-form">
          <fieldset class="fieldset">
            <legend class="description">   .</legend>
            <label hidden for="name"></label>
            <input
              id="item-name-input"
              class="item-input styled-input"
              name="name"
              placeholder=""
              type="text"
              maxlength="${ITEM.NAME.MAX_LENGTH}"
              required
              autofocus
            >
            <label hidden for="price"></label>
            <input
              id="item-price-input"
              class="item-input styled-input"
              name="price"
              placeholder=""
              type="number"
              step="${MONEY_UNIT}"
              min="${ITEM.PRICE.MIN}"
              max="${ITEM.PRICE.MAX}"
              required
            >
            <label hidden for="quantity"></label>
            <input
              id="item-quantity-input"
              class="item-input styled-input"
              name="quantity"
              placeholder=""
              type="number"
              step="1"
              min="${ITEM.QUANTITY.MIN}"
              max="${ITEM.QUANTITY.MAX}"
              required
            >
          </fieldset>
          <button class="add-item-button styled-button emphasized"></button>
        </form>
      </section>
      <section>
        <h2 class="table-title"> </h2>
        <table class="styled-table">
          <colgroup>
            <col style="width: 30%">
            <col style="width: 20%">
            <col style="width: 20%">
            <col style="width: 30%">
          </colgroup>
          <thead>
            <tr class="styled-tr">
              <th class="styled-th"></th>
              <th class="styled-th"></th>
              <th class="styled-th"></th>
              <th class="styled-th"></th>
            </tr>
          </thead>
        </table>
        <div class="scrollable">
          <table class="styled-table no-border-top">
            <colgroup>
              <col style="width: 30%">
              <col style="width: 20%">
              <col style="width: 20%">
              <col style="width: 30%">
            </colgroup>
            <tbody>
              ${items
                .map(
                  ({ name, price, quantity }) => `
                    <tr
                      is="item-row"
                      tab="manage"
                      class="styled-tr"
                      name="${name}"
                      price="${price}"
                      quantity="${quantity}"
                    >
                    </tr>
                  `
                )
                .join('')}
            </tbody>
          </table>
        </div>
      </section>
      <div class="snackbar"></div>
    `;
  }

  setEvent() {
    this.addEvent('submit', '#item-add-form', (event) => {
      event.preventDefault();

      const { target } = event;
      const item = {
        name: target.querySelector('#item-name-input').value.trim(),
        price: target.querySelector('#item-price-input').valueAsNumber,
        quantity: target.querySelector('#item-quantity-input').valueAsNumber,
      };

      try {
        vendingMachine.addItem(item);
      } catch (err) {
        showSnackBar(err);
      }
    });
  }
}

customElements.define('item-management', ItemManagementPage);
